.CastList {
  grid-gap: 16px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  list-style: none;
  margin: 30px auto;

  max-width: calc(100vw - 48px);
  padding: 0;
}

.CastItem {
  border-radius: 2px;
  box-shadow: 0 1px 3px 0 rgb(0 0 0 / 20%), 0 1px 1px 0 rgb(0 0 0 / 14%),
    0 2px 1px -1px rgb(0 0 0 / 12%);
}

.CastInfo {
  /* text-align: center; */
  padding: 10px;
}

.CastText {
  color: #000;
  font-size: 12px;
  font-weight: normal;
  margin-top: 5px;
}

.CastTitle {
  font-size: 18px;
  font-weight: bold;
}

.CastSubTitle {
  font-weight: bold;
}
